import {common} from "../../utils/api/url";

import mixins from "~/utils/mixins";
export default {
  name: 'appIndex',
  layout: 'defaultAppScreen',
  mixins: [mixins],
  data(){
    return {
      active: 6,
      serverAppList: [],
      noticeAppList: [],
      serchName: ''
    }
  },
  mounted() {
    this.$nextTick(() => {

    });
  },
  created() {
    this.active = this.$route.query.activeType ? this.$route.query.activeType : 6;
    if (this.active == 6){
      this.initAppRecommend();
    }else {
      this.initAppServer();
    }
  },
  methods: {
    layoutInit(){

    },
    initAppRecommend(value){
      let params = {
        searchKey: value
      };
      this.$axios.get(common.server_list_list3, {params: params}).then(res => {
        if (res.data.data){
          this.serverAppList = res.data.data;
        }
      });
    },
    initAppServer(value){
      let params = {
        appletType: this.active,
        searchKey: value
      };
      this.$axios.get(common.server_list_list, {params: params}).then(res => {
        if (res.data.data){
          this.serverAppList = res.data.data;
        }
      });
    },
    serverBlock(event, item){
      if (item == 'all'){

      }else {
        this.$router.push({
          path: '/app/appServer',
          query: {
            id: item.id,
            activeType: this.active,
            userType: this.loginUserAppType,
            navH: this.navHeight,
            appType: this.globalAppShow
          }
        });
      }
    },
    activeTabMenu(name){
      this.noticeAppList = [];
      this.serverAppList = [];
      this.serchName = '';
      if (name == 5){
        this.$router.push({
          path: '/app/appMyNotice',
          query: {
            id: parseInt(name),
            userType: this.loginUserAppType,
            navH: this.navHeight,
            appType: this.globalAppShow
          }
        });
      }else if (name == 6){
        this.active = name;
        this.initAppRecommend();
      }else {
        this.active = name;
        this.initAppServer();
      }
    },
    returnIndex(){
      this.$router.push({
        path: '/app/appIndex',
        query: {
          activeType: this.$route.query.activeType,
          userType: this.loginUserAppType,
          navH: this.navHeight,
          appType: this.globalAppShow
        }
      });
    },
    onSearch(value){
      console.log(value);
      if (this.active == 6){
        this.initAppRecommend(value);
      }else {
        this.initAppServer(value);
      }
    },
    onClear(){

    }
  }
}
